Body {
  color: white;
  background-color: black;
  user-select: none;
}

#mapparent {
  position: relative;
  user-select: none;
}

#mapimg {
  position: relative;
  width: 100%;
  height: auto;
}

#mapico {
  position: absolute;
  width: 32pt;
  height: 32pt;
  transform: translate(-50%, -50%);
  animation: Target-pulse infinite 1s linear;
  transition: all .5s ease-in-out;
}

@keyframes Target-pulse {
  0% {
    filter: invert(0%);
  }

  50% {
    filter: invert(100%);
  }

  100% {
    filter: invert(0%);
  }
}